.end-links-pic {
  display: flex;
  flex-direction: column;
  justify-items: flex-end;
  align-items: flex-end;
}

.end-links {
  border-left: 1px solid black;
  height: 100%;
}
.end-links,
.middle-content {
  display: flex;
  margin-left: auto;
  justify-items: center;
  align-items: center;
}

.content-main-proj-ul:hover,
.load-next:hover {
  cursor: pointer;
}

.proj-container {
  display: flex;
  flex-direction: column;
  height: 13.3vh;
}

.proj-title-bg {
  font-size: 4vh;
  display: flex;
  text-align: center;
  opacity: 0.2;
  width: 100%;
  margin-top: -13.3vh;
  align-items: center;
  justify-content: center;
}
.proj-hover {
  border: 1px solid white;
  font-size: 2vh;
  height: 13.3vh;
  background: linear-gradient(to right, #06beb6, #48b1bf);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content-main-proj > ul {
  margin: auto;
  margin-left: -40px;
}

.content-main-proj-ul > li {
  width: 100%;
  height: 13.3vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  list-style-type: none;
}
.content-main-proj {
  height: 80vh;
  width: 100%;
}

.load-prev,
.load-next {
  display: flex;
  flex-direction: column;
  height: 10vh;
  background-color: black;
  color: #06beb6;
  width: 100%;
  justify-content: center;
}

.flex-container,
.contact-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  align-items: center;
}

#contact-first {
  max-width: 50vh;
}

.contact-container > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 100vh;
}

.contact-link {
  border: 1px solid grey;
  font-size: 3vh;
  height: 50vh;
  justify-content: space-between;
}
.contact-container a {
  color: white;
  text-decoration-color: paleturquoise;
}

.contact-link:hover,
.contact-container a:hover {
  color: rgba(255, 255, 255, 0.7);
  border-color: slateblue;
  text-decoration-color: slateblue;
}
.click-me:hover {
  cursor: pointer;
  color: rgba(0, 0, 0, 0.5);
  border-top-left-radius: 15vh;
  border-bottom-left-radius: 00px;
}
.click-me {
  color: white;
  background: paleturquoise;
}
.arrow-down {
  display: flex;
  font-size: 1.5vh;
  background-color: tomato;
}

.together {
  display: flex;
  overflow-y: hidden;
  background: silver;
}
.rightside_container {
  display: flex;
  color: black;
  max-width: 5vh;
}

.rightside_items {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.rightside_items > div {
  display: flex;
  text-align: center;
  padding: 5px;
}

.rightside_items > div:hover {
  opacity: 0.5;
}

.container {
  scroll-snap-type: y mandatory;
  border-radius: 000px;
  height: 100vh;
  margin-left: -8vh;
  border-top-left-radius: 0vh;
  border-bottom-left-radius: 00px;
  overflow-y: auto;
  overflow-x: hidden;
  width: 100%;
  font-family: "Ink Free", fantasy, cursive;
}

.container::-webkit-scrollbar,
.together::-webkit-scrollbar {
  display: none;
}

.container > div {
  border: 3px solid black;
  border-left: 0px;
  border-bottom: none;
  scroll-snap-align: start;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  font-size: 5vh;
  text-align: center;
}
.first {
  display: flex;

  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin-top: auto;
}

.first h1,
.first h3 {
  margin: 0;
  font-size: 5vh;
  align-self: center;
  font-family: "Ink Free", fantasy, cursive;
}

.first h3 {
  font-size: 3vh;
}

.second {
  display: flex;
  flex-direction: column;
  margin-top: auto;
  padding-left: 5vh;
  transform: rotate(270deg);
  padding-bottom: 3vh;
}

.Nest {
  display: flex;
  flex-direction: row;
  border: 1px solid blue;
  margin-top: auto; /* In flex, removes white space */

  align-self: stretch;
}

.Nested,
.Nested2 {
  width: 200px;
  height: 100px;
  flex: 1;
}

/*
* NAVIGATION CSS
*/
.nav_container {
  width: 100%;
  margin: 20px auto;
}

.center {
  text-align: center;
}

.nav h1 {
  margin-left: 20px;
  font-family: "Ink Free", cursive;
  color: black;
}
.nav {
  box-shadow: 0px 2px 24px 0px rgba(0, 0, 0, 0.15);
  display: flex;
  justify-content: center;
  width: 100%;
}

.div_links {
  display: flex;
}

.nav_links {
  margin: auto;
}

.nav_links li {
  display: flex;
  border-bottom: 3px solid black;
  font-family: "Ink Free", fantasy, cursive;
  font-size: 14px;
}

.nav_links a,
.nav a {
  text-decoration: none;
  color: white;
}

.nav li:hover {
  background-color: rgba(200, 99, 71, 0.5);
}

/*
* ABOUT ME CSS
*/
.about-me-grid {
  background: #06beb6; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #48b1bf,
    #06beb6
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #48b1bf,
    #06beb6
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  text-align: center;
  font-family: "Ink Free", fantasy, cursive;
}
.about-me-cell {
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding-top: 8vh;
  height: 38vh;
}
.about-me-cell p {
  font-size: 3vh;
  margin-top: 0;
}
.about-me-cell h2 {
  font-size: 5vh;
  font-weight: bold;
  color: white;
  letter-spacing: 10px;
  font-family: "Ink Free", fantasy, cursive;
  width: 60%;
  margin: auto;
}
.about-me-cell hr {
  width: 60%;
}
.paragraph {
  font-size: 3vh;
  text-align: left;
  letter-spacing: 1.5px;
  height: 50vh;
  padding-top: 5vh;
}
.paragraph p {
  color: white;
  margin: auto;
  border-radius: 10px;
  border-top: 2px double rgba(255, 255, 255, 0.6);
  border-bottom: 2px double rgba(255, 255, 255, 0.6);
  border-width: thick;
  background-color: rgba(0, 0, 0, 0.5);
  text-align: center;
}

/* 
* For Average screens at least 720px 
*/
@media screen and (min-width: 720px) {
  .middle-content {
    max-width: 80%;
  }
  #contact-first {
    max-width: 100vh;
  }

  .rightside_items > div {
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .nav_links {
    display: flex;
    justify-content: center;
  }

  .nav_links li {
    border-bottom: none;
    padding: 28px 30px 22px 30px; /* top right bottom left */
    display: inline;
    font-size: 20px;
  }

  .about-me-cell p {
    padding-bottom: 70px;
  }

  .about-me-cell hr {
    width: 30%;
  }
  .paragraph p {
    width: 30%;
  }
}

/* 
* For wide screens at least 1080px 
*/
@media screen and (min-width: 1080px) {
  .proj-hover {
    font-size: 3vh;
  }
  .end-links p {
    font-size: 2vh;
  }
}

/* 
* For Ultra-wide screens at least 3440px 
*/
@media screen and (min-width: 3440px) {
}
